.titleResults {
  color: goldenrod;
  font-size: 50px;
  background-color: lightgrey;
  width: 280px;
  border-radius: 50%;
  font-family: "HARRYP", sans-serif;
  margin: 15px;
}

.section {
  width: 100vw;
}

.list {
  margin: 20px;
}

@media all and (min-width: 768px) {
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 20px;
  }
}

.eachLi {
  margin-bottom: 15px;
  background-image: url("../images/wallpCharacter.jpg");
  background-position: center;
  background-size: cover;
  width: 300px;
  margin: 20px;
}
